:root {
  --background: #e5cde9ff;
  --textprimary: #011627ff;
  --primary: #2ec4b6ff;
  --secondary: #9a348eff;
  --primary01: #ed6a5aff;
}

* {
  font-family: "Lato", sans serif;
}

body {
  margin: 0;
}

@keyframes wave-animation {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(14deg);
  }
  20% {
    transform: rotate(-8deg);
  }
  30% {
    transform: rotate(14deg);
  }
  40% {
    transform: rotate(-4deg);
  }
  50% {
    transform: rotate(10deg);
  }
  60% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
